import React from 'react';
import {TitleLine} from "../../../components/titleLine/TitleLine";
import {SectionTitle, SectionTitleBox} from "../../../components/sectionTitleBox/SectionTitleBox";
import {Link} from "../../../components/link/Link";
import photo from '../../../assets/main/photo.png'
import {Container} from "../../../components/container/Container";
import {S} from './Aboutme-styles'
import styled from "styled-components";


export const AboutMe:React.FC = () => {
    return (
        <AboutMeBox>
            <Container>
                <SectionTitleBox>
                    <SectionTitle><span>#</span> </SectionTitle>
                    <TitleLine width='60'/>
                </SectionTitleBox>
                <S.AboutMeBody>
                    <div>
                        <S.AboutMeText>
                            <p>,   ,    .   :   ,
                                    -.</p>
                            <p>    ?         
                                ,      ,    , 
                                 ,     ,   ,  
                                
                                   ,     .
                                    ,     
                                 .
                                     ,     ,
                                  , ,      .</p>
                            <p>    ,   -    
                                ,
                                      .</p>
                        </S.AboutMeText>
                        <Link value={' ->'} href={'#'}/>
                    </div>
                    <S.AboutMeImg>
                        <img src={photo} alt="photo"/>
                        <TitleLine width={'75'}/>
                    </S.AboutMeImg>
                </S.AboutMeBody>
            </Container>
        </AboutMeBox>
    );
};

const AboutMeBox = styled.section`
  position: relative;`

